"use client"
import { useState } from "react";
import axios from "axios";

const Upload = () => {
    const [files, setFiles] = useState([]);
    const [loading, setLoading] = useState(false);

    const handleFileChange = (e) => {
        setFiles(e.target.files);
    };

    const handleUpload = async () => {
        const formData = new FormData();
        for (let i = 0; i < files.length; i++) {
            formData.append("files", files[i]);
        }

        setLoading(true);
        try {
            const res = await axios.post("http://localhost:8000/upload-resumes", formData, {
                headers: { "Content-Type": "multipart/form-data" },
                responseType: "blob", // important to receive file
            });

            // Trigger download of Excel
            const url = window.URL.createObjectURL(new Blob([res.data]));
            const link = document.createElement("a");
            link.href = url;
            link.setAttribute("download", "resumes.xlsx");
            document.body.appendChild(link);
            link.click();
            link.remove();
        } catch (err) {
            console.error(err);
            alert("Upload failed");
        } finally {
            setLoading(false);
        }
    };

    return (
        <div>
            <h2>Upload Resumes</h2>
            <input type="file" multiple onChange={handleFileChange} />
            <button onClick={handleUpload} disabled={loading}>
                {loading ? "Processing..." : "Upload & Generate Excel"}
            </button>
        </div>
    );
};

export default Upload;
